<template>
	<view class="container">
		<tui-sticky :scrollTop="scrollTop" stickyHeight="80rpx">
			<template v-slot:header>
				<view class="subsection-box">
					<u-subsection :list="subsectionList" fontSize="16" mode="button" activeColor="#000" bgColor="#9fc6ff" :current="currentSubsection" @change="sectionChange"></u-subsection>
				</view>
			</template>
		</tui-sticky>
		<template v-if="currentSubsection == 0 ? true : false">
			<view class="collect-content" >
				<view class="collect-content-item" v-for="(item,index) in 22" :key="index">
					<view class="collect-image">
						<image
							src="https://img11.360buyimg.com/n1/jfs/t1/176706/37/23229/183217/61b32f0dE18e2603d/d1064d1e01f321f8.jpg"
							style="width: 100%;height: 100%; border-radius: 10rpx;"></image>
					</view>
					<view class="collect-desc">
						<view class="collect-top">
							<view class="collect-title">
								<text>沃野发光轻音电竞游戏鼠标</text>
							</view>
						</view>
						<view class="collect-action">
							<view class="collect-price">
								<text class="collect-price-unit">￥</text>
								<text class="collect-price-num">33.90</text>
							</view>
							<view class="collect-cancel" @click="cancelCollection(item)">
								<text>取消收藏</text>
							</view>
						</view>
					</view>
				</view>
			</view>
		</template>
		<template  v-else>
			<view class="collect-content" >
				<view class="collect-content-item" v-for="(item,index) in 6" :key="index">
					<view class="collect-image">
						<image
							src="https://img12.360buyimg.com/n1/jfs/t1/110082/16/35719/110417/63e02074Fe4d7cfc2/3c3c2dec659748e0.jpg"
							style="width: 100%;height: 100%; border-radius: 10rpx;"></image>
					</view>
					<view class="collect-desc">
						<view class="collect-top">
							<view class="collect-title">
								<text>麦多多旗舰店</text>
							</view>
						</view>
						<view class="collect-action">
							<view class="collect-price">
								<text class="collect-price-num">2023-2-22</text>
							</view>
							<view class="collect-cancel" @click="cancelCollection(item)">
								<text>取消收藏</text>
							</view>
						</view>
					</view>
				</view>
			</view>
		</template>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				scrollTop: 0,
				subsectionList: ['商品收藏', '店铺收藏'],
				currentSubsection: 0
			};
		},
		onPageScroll(e) {
			this.scrollTop = e.scrollTop
		},
		methods: {
			sectionChange(index) {
				this.currentSubsection = index
			},
			// 取消收藏
			cancelCollection(item){
				uni.showToast({
					title:"取消收藏->" + item,
					icon:'none'
				})
			}
		}
	}
</script>

<style lang="scss">
	.container {
		width: 100%;
		background-color: #f6f6f6;
		display: flex;
		flex-direction: column;
		
		.subsection-box{
			width: 98%;
			margin: 0 auto;
		}
		
		.collect-content{
			width: 95%;
			margin: 0 auto;
			margin-top: 15rpx;
			margin-bottom: 20rpx;
			border-radius: 10rpx;
			display: flex;
			flex-wrap: wrap;
			
			.collect-content-item{
				width: 40%;
				height: 380rpx;
				margin: 0 auto;
				padding: 15rpx;
				margin-top: 20rpx;
				background-color: #fff;
				border-radius: 10rpx;
				
				.collect-image{
					width: 90%;
					height: 73%;
					margin: 0 auto;
				}
				
				.collect-desc{
					display: flex;
					flex-direction: column;
					width: 93%;
					margin: 0 auto;
					margin-top: 20rpx;
					
					.collect-top{
						display: flex;
						flex-direction: row;
						width: 100%;
						
						.collect-title{
							width: 95%;
							margin: 0 auto;
							margin-left: 10rpx;
							font-size: 10px;
						}
						
					}
					
					.collect-action{
						width: 90%;
						margin: 0 auto;
						margin-top: 10rpx;
						margin-bottom: 20rpx;
						display: flex;
						flex-direction: row;
						
						.collect-price{
							width: 60%;
							margin: 0 auto;
							color: red;
							
							.collect-price-unit{
								font-size: 11px;
							}
							
							.collect-price-num{
								font-size: 12px;
								font-weight: bold;
							}
						}
						
						.collect-cancel{
							width: 40%;
							margin: 0 auto;
							margin-top: 12rpx;
							font-size: 10px;
							color: red;
						}
					}
				}
			}
		}
	}
</style>
